.App {
  width: 100vw;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-areas:
    "banner banner"
    "hikers-txt hikers-img"
    "momandboy-img momandboy-img"
    "choices-txt choices-btns"
    "girl-img girl-img"
    "reviews-title reviews-txt"
    "footer footer";
  font-family: 'Open Sans', sans-serif;
}
.h60 {
  height: 60vh;
}
.h70 {
  height: 70vh;
}
.h100 {
  height: 100vh;
}
.purple-links-hdrs {
  color: rgba(97, 50, 118, 1);
}
a {
  text-decoration: none;
}
button {
  outline: none;
  &::-moz-focus-inner {
    border: 0;
  }
  /* Don't forget! User accessibility is important */
  &:focus {
    /* put custom focused styles here */
    outline: 1px dotted white;
  }
}

