.Choices-txt-section {
  grid-area: choices-txt;
  padding: 30% 10% 0 30%;
}
.Choices-title {
  font-size: calc(2vw + 6px);
}
.Choices-txtcontent {
  font-size: calc(1vw + 0.5px);
  padding: 5px 0;
}
.Choices-link {
  font-size: calc(1vw + 4px);
}

.Choices-btns {
  grid-area: choices-btns;
  background: rgba(250, 250, 250, 1);
  width: 100%;
  height: 50vw;
  padding: 10vw 0 0;
  display: flex;
  flex-wrap: wrap;
}
.Choices-btn-wrapper {
  width: 40%;
  height: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  cursor: pointer;
  transition: all 0.3s ease;
  &:hover {
    transform: scale(1.2);
  }
}
.Choices-svg-wrapper {
  width: 50%;
  padding: 12%;
  border: 1px solid rgba(172, 95, 208, 1);
  border-radius: 50%;
}
.Choices-btn-txt {
  font-size: calc(1vw + 5px);
}
